               }
               
               .circle {
                 width: 135px;
                 margin: 86px 6px 20px;
                 display: inline-block;
                 position: relative;
                 text-align: center;
                 line-height: 1.2;
               }
               
               .circle canvas {
                 vertical-align: top;
               }
               
               .circle {
                 strong{
                 position: absolute;
