     .gallery{
         background: $bgc--color-primary_4;
         .gallery_title{
             text-align: center;
             h3{
                 margin: 79px 0 30px;
                 @include font(20px, $font-ssp-regular, $font--color-primary_2, 12px);
                 font-weight: 300;
                 letter-spacing: 4px;
                 text-transform: uppercase;
             }
             p{
              @include font(14px, $font-open_sans-regular, $font--color-primary_3, 25px);
              font-weight: 400;
              letter-spacing: 0.7px; 
              margin: 30px 0;
             }
         }
         .gallery_filter{
             text-align: center;
             padding: 12px 0;
             ul{
                 margin: 0;
                 padding: 14px 0;
                 li{
                     transition: 0.6s linear;
                     padding: 0 13px;
                     display: inline-block;
                     a{
                         font-weight: 300;
                         @include font(16px, $font-open_sans-regular, $font--color-primary_6, 12px);
                         &::after{
                             content: '';
                             display: block;
                             width: 100%;
                             height: 1px;
                             background: $font--color-primary_4;
                             transform: scale(0);
                             transition: 0.6s;
                         }
                         &:hover{
                            cursor: pointer;
                         }
                         &:hover:after{
                             transform: scale(1.0);
                         }
                     }
                 }
                 .active{
                     border-bottom: 1px solid $font--color-primary_4;
                 }
             }
         }
         .gallery_container{
             padding: 10px 0;
             width: 100%;
             // height: 100vh;
             .gallery_item{
                 display: grid;
                 grid-template-columns: repeat(4, minmax(20px, 1fr));
                 grid-template-rows: repeat(20, 2vw);
                 grid-gap: 10px;
                 .gallery_item_box{
                     position: relative;
                     width: 100%;
                     height: 100%;
                     .gallery_hover{
                         // display: none;
                         opacity: 0;
                         padding: 59px 36px;
                         top: 0;
                         transition: 0.3s ease-in;
                         transform: scale(0.1);
                         position: absolute;
                         width: 100%;
                         height: 100%;
                         background: rgba($color: #000000, $alpha: 0.7);
                         h3{
                             @include font(20px, $font-open_sans-regular, $font--color-primary_4, 12px);
                             font-weight: 300;
                             line-height: 46px;
                             letter-spacing: 4px;
                             text-transform: uppercase;
                             &::after{
                                 content: '';
                                 display: block;
                                 border-bottom: 1px solid $font--color-primary_4;
                                 width: 85%;
                             }
                         }
                         p{
                             @include font(12px, $font-open_sans-regular, $font--color-primary_9, 20px);
                             font-weight: 400;
                             letter-spacing: 0.6px;
                             margin: 26px 0;
                         }
                         button{
                             text-align: center;
                             padding: 3px 0;
                             width: 29px;
                             border: none;
                             outline: none;
                             height: 29px;
                             border-radius: 50%;
                             background: $bgc--color-primary_6;
                             color: $font--color-primary_1;
                             transition: 0.3s ease-in;
                             &:hover{
                                 color: $font--color-primary_4;
                                 background: none;
                             }
                         }
                     }
                     img{
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                     }
                     &:hover{
                         .gallery_hover{
                             opacity: 1;
                             transition: 0.4s ease-in-out;
                             transform: scale(1);
                             h3{
                                 &:after{
                                     width: 12%;
                                     transition: 1.1s ease-in-out;
                                 }
                             }
                         }
                     }
                 }
                 #item-one{
                     grid-column: 1/2;
                     grid-row: 1/11;
                 }
                 #item-two{
                     grid-column: 2/2;
                     grid-row: 1/7;
                     .gallery_hover{
                         padding: 20px 36px;
                         p{
                             margin: 20px 0;
                         }
                     }
                 }
                 #item-three{
                     grid-column: 3/3;
                     grid-row: 1/9;
                 }
                 #item-four{
                     grid-column: 4/4;
                     grid-row: 1/11;
                 }
                 #item-five{
                     grid-column: 1/2;
                     grid-row: 21/11;
                 }
                 #item-six{
                     grid-column: 2/3;
                     grid-row: 7/16;
                 }
                 #item-seven{
                     grid-column: 3/4;
                     grid-row: 16/21;
                 }
                 #item-eight{
                     grid-column: 4/4;
                     grid-row: 19/11;
                 }
                 #item-nine{
                     grid-column: 3/4;
                     grid-row: 9/15;
                     .gallery_hover{
                         padding: 23px 36px;
                         p{
                             margin: 18px 0;
                         }
                     }
                 }
                 #item-ten{
                     grid-column: 4/4;
                     grid-row: 11/21;
                 }
                 #item-eleven{
                     grid-column: 2/2;
                     grid-row: 16/21;
                     .gallery_hover{
                         padding: 1px 31px;
                         p{
                             margin: 12px 0;
                         }
                     }
                 }
                 #item-twelve{
                     grid-column: 3/3;
                     grid-row: 21/15;
                     .gallery_hover{
                         padding: 20px 36px;
                         p{
                             margin: 18px 0;
                         }
                     }
                 }
             }
         }
